//添加一个响应之前拦截器
axios.interceptors.response.use(
  (res) => {
    //res: 服务器响应数据
    // console.log('响应数据返回给前端之前');

    let loadingInstance1 = Loading.service({
      lock: true,
      text: "Loading",
      spinner: "el-icon-loading",
      background: "rgba(0, 0, 0, 0.7)",
    });
    loadingInstance1.close();
    //关闭加载提示
    // Toast.clear();
    // Loading({
    //   lock: true,
    //   text: 'Loading',
    //   spinner: 'el-icon-loading',
    //   background: 'rgba(0, 0, 0, 0.7)'
    // });
    // setTimeout(() => {
    //   loading.close();
    // }, 2000);

    return res;
  },
  (err) => {
    console.log("err ==> ", err);

    //关闭加载提示
    // Toast.clear();

    return Project.reject(err);
  }
);
